.addNewSection{
    padding: 20px 30px;
    background: #f2f2f2;
    text-align: center;
    cursor: pointer;
    svg{
        width: 30px;
    }
}
.each_section_Wrp{
    text-align: center;
    border: 1px solid #eee;
    padding: 20px;
    cursor: pointer;
    border-radius: 20px;
    -webkit-transition: all ease-in-out 300ms;
    transition: all ease-in-out 300ms;
    .aicon{
        width: 80px;
        height: 80px;
        // background: #f2f2f2;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        svg{
            width: 40px;
            height: 40px;
            color: #c3c3c3;
        }

    }
    .section_title{
        font-size: 18px;
        font-weight: 500;
    }
   
}

.each_section_Wrp:hover, .each_section_Wrp.active{
    transform: scale(1.1);
    .aicon{
        svg{
            color: #fff;
        }
    }
    .section_title{
        color: #fff; 
    }
    background: #9B4DFF;    
}
.allSectionTypeWrp{
    border: 1px solid #eee;
    padding: 30px;
    position: relative;
}
.removeBtn {
    position: absolute;
    top: 10px;
    right: 10px;
    svg{
        width: 30px;
    }
}

.each_sections{
    position: relative;
    margin-bottom: 20px;
    box-shadow: 0px 5px 10px -2px rgba(0,0,0,0.2);
    .each_section_header{
        background: var(--cui-primary);
        .form-control{
            background: transparent;
            border: none;
            border-radius: 0;
            color: #fff;
            &:focus{
                outline: none;
            }
        }
    }
    .each_section_body{
        padding: 20px;
    }
}

.each_repeater_item{
    border: 1px solid #eee;
    padding: 20px;
    position: relative;
}
.each_single_items {
    position: relative;
    padding: 20px;
    border: 1px solid #dbdbdb;
}

.each_single_items:hover , .each_repeater_item:hover{
    outline: 2px solid  var(--cui-info);
    .arPlusIcon, .arRemoveIcon {
        opacity: 1;
        visibility: visible;
    }
}


.arPlusIcon , .arRemoveIcon {
    position: absolute;
   
    width: 40px;
    height: 40px;
    border-radius: 50%;   
    border: none;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all ease-in-out 300ms;
    z-index: 2;

    svg{
        width: 25px;
    }
   
}
.arPlusIcon {
    background: var(--cui-info);
    left: 50%;
    bottom: -20px;
    transform: translateX(-50%);
}
.arRemoveIcon {
    background: #ff5400;
    right: -20px;
    top: 50%;
    transform: translateY(-50%);
    color: #ffffff;
}

.form-control:focus{
    box-shadow: none;
}
.copySectionProp{
    position: absolute;
    top: 5px;
    right: 10px;
    color: #fff;
}
.each_repeater_wrapper {
    position: relative;
    border: 1px dashed #898989;
    padding: 20px;
    +.each_repeater_wrapper {
        margin-top: 20px;
    }
}
.copySectionProp{
    cursor: pointer;
    svg{
        width: 22px;
        height: 22px;
    }
}